import { action, computed, makeObservable, observable } from "mobx";

export default class StoreCard {

	_listCard = [];
	_sizeCard = 20;

	constructor() {
		makeObservable(this, {
			_listCard: observable,
			_sizeCard: observable,
			listCard: computed,
			sizeCard: computed,
			addCard: action,
			clearList: action,
			removeCard: action,
			changeSize: action
		});
	}

	get sizeCard() {
		return this._sizeCard;
	}

	get listCard() {
		return this._listCard;
	}

	addCard(card) {
		this._listCard.push({ ...card, id: Date.now() });
	}

	clearList() {
		this._listCard = [];
	}

	removeCard(card) {
		this._listCard = this._listCard.filter(c => c.id !== card.id);
	}

	changeSize(e) {
		this._sizeCard = e.target.value;
	}
}