import { observer } from "mobx-react";
import { storeCard, cards } from "../init";

export const Nav = observer(() => {

	const clickAddCard = (card) => storeCard.addCard(card);
	const clickClearListCard = () => storeCard.clearList();
	const changeSize = (e) => storeCard.changeSize(e);

	return <nav className="navbar bg-light">
		<div className="container-fluid">
			<h1 className={"text-center"}>Общаемся вместе</h1>
			<button className="navbar-toggler" type="button" data-bs-toggle="offcanvas"
				data-bs-target="#offcanvasNavbar" aria-controls="offcanvasNavbar">
				<span className="navbar-toggler-icon"></span>
			</button>
			<div className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasNavbar"
				aria-labelledby="offcanvasNavbarLabel">
				<div className="offcanvas-header">
					<h5 className="offcanvas-title" id="offcanvasNavbarLabel">Карточки</h5>
					<button type="button" className="btn-close" data-bs-dismiss="offcanvas"
						aria-label="Close"></button>
				</div>
				<div className="offcanvas-body">
					<div className="accordion" id="accordionExample">
						<div className="accordion-item">
							<h2 className="accordion-header" id="headingOne">
								<button className="accordion-button" type="button" data-bs-toggle="collapse"
									data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
									Действие
								</button>
							</h2>
							<div id="collapseOne" className="accordion-collapse collapse show"
								aria-labelledby="headingOne" data-bs-parent="#accordionExample">

								{cards.listCard.filter(m => m.group === "action").map(m => {
									return <div className={"row"} key={m.src}>
										<div className={"col-8"}>
											<img src={m.src} className="card-img-top" alt={""} onClick={() => clickAddCard(m)} style={{ width: "50%" }} />
										</div>
										<div className={"col align-self-center f-strong"}>
											{m.header}
										</div>
									</div>
								})}

							</div>
						</div>
						<div className="accordion-item">
							<h2 className="accordion-header" id="headingTwo">
								<button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
									data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
									Предмет
								</button>
							</h2>
							<div id="collapseTwo" className="accordion-collapse collapse" aria-labelledby="headingTwo"
								data-bs-parent="#accordionExample">
								<div className="accordion-body">

								</div>
							</div>
						</div>

					</div>

					<div>
						<label htmlFor="customRange3" className="form-label mt-5">Размер карточки {Math.ceil((storeCard.sizeCard / 20) * 100) + "%"}</label>
						<input type="range" className="form-range" min="14" max="30" step="2" id="customRange3" onChange={changeSize} value={storeCard.sizeCard} />
					</div>

					<button className={"btn btn-primary my-5"} onClick={clickClearListCard}>Очистить поле</button>

				</div>
			</div>
		</div>
	</nav>;
});