import { observer } from "mobx-react";
import { storeCard } from "../init";
import "../css/card.css";

export const Card = observer((props) => {
	const close = () => storeCard.removeCard(props.card);
	const size = storeCard.sizeCard + "rem";

	return <div className="card" style={{ width: size }}>
		<img src={props.card.src} className="card-img-top" alt={props.card.title} />
		<div className="card-body">
			<h5 className="card-title">{props.card.header}</h5>
			<button className={"btn btn-outline m-0 btn-light card-close"} title={"Удалить"} onClick={close}>X</button>
		</div>
	</div>;
});